
import Divider from 'vux/src/components/divider/index.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Checker from 'vux/src/components/checker/checker.vue'


import {
  date
} from '../db/contents.js'

export default {
  components: {
    Divider,
    CheckerItem,
    Checker
  },
  data() {
    return {
      top: this.$utils.storage.getNewTop()
    }
  },
  methods: {
    changeItem: function(itemValue, itemDisabled){
      this.$utils.storage.saveNewTop(itemValue)
    }
  }
}



